<template>
  <div id="indexmain">
    <el-row id="search" :gutter="24">
      <el-col :offset="6" :sm="12" :md="12" :lg="12" :xl="12">
        <p>
          <img src="./../assets/logo.png">
          <span>狗敦A季</span>
        </p>
        <div class="inp">
          <el-input placeholder="请输入电影名、或网址" class="inpu" v-model="key">
             <el-button class="inpb" v-on:click="searchM" slot="append" icon="el-icon-search">搜索</el-button>
          </el-input>
        </div>
      </el-col>
    </el-row>
    <el-row :gutter="24" id="copyright">
      <el-col :span="24" :offset="0">
        <div class="con">
          <a target="_blank" href="https://jiawanlong.github.io/">个人博客</a>
          <a target="_blank" href="http://wejser.com">个人网站</a>
          <a target="_blank" href="http://book.wejser.com">个人简书</a>
          <a target="_blank" href="https://github.com/jiawanlong">GitHub</a>
        </div>
        <div class="con">
          <span>Copyright 狗敦A季</span>
          <span>豫ICP备17035620号</span>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      key: ""
    };
  },
  filters: {},
  mounted() {},
  methods: {
    searchM() {
      if (this.key.trim() == "") {
        this.$notify({
          title: "狗敦A季提醒您",
          message: "请输入电影名或电影地址",
          type: "warning"
        });
      } else {
        if (
          this.key.trim().indexOf("http://") > -1 ||
          this.key.trim().indexOf("https://") > -1
        ) {
          this.$router.push({
            name: "movie",
            params: {
              url: this.key
            }
          });
        } else {
          this.$router.push({
            name: "main",
            params: {
              title: this.key
            }
          });
        }
      }
    }
  }
};
</script>

<style lang="less">
.el-input-group__append {
  background: #2196f3 !important;
  color: #fff;
  border: 1px solid #2196f3;
  cursor: pointer;
  letter-spacing: 5px;
}
#indexmain {
  height: 100%;
  overflow: hidden;
  font-family: "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑",
    Arial, sans-serif;
  #search {
    height: 35%;
    margin-top: 9%;
    p {
      text-align: center;
      img {
        width: 72px;
        vertical-align: middle;
      }
      span {
        font-size: 28px;
        letter-spacing: 0.2rem;
        color: #2196f3;
        margin-left: 2%;
      }
    }
    .el-col {
      border-radius: 5px;
    }
    .inp {
      width: 80%;
      margin: 0 auto;
      margin-top: 5%;
      input {
        height: 45px;
        font-size: 15px;
      }
      .inpu {
        border: 1px solid #2196f3 !important;
        border-radius: 3px;
      }
    }
  }
  #copyright {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    .con {
      height: 28px;
      line-height: 28px;
      text-align: center;
      color: #4e4a4a;
      font-size: 13px;
      a {
        text-decoration: none;
        color: #4e4a4a;
        margin: 0 30px;
      }
      span {
        margin: 0 5px;
      }
    }
  }
}
@media (max-width: 970px) {
  #indexmain {
    height: 100%;
    overflow: hidden;
    font-family: "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei",
      "微软雅黑", Arial, sans-serif;
    #search {
      height: 35%;
      width: 90%;
      margin-top: 22%;
      width: 90%;
      padding: 0;
      margin-left: 5% !important;
      font-size: 16px;
      > div {
        margin: 0;
        padding: 0;
      }
      p {
        text-align: center;
        img {
          width: 50px;
          vertical-align: middle;
        }
        span {
          font-size: 22px;
          color: #2196f3;
          margin-left: 2%;
        }
      }
      .el-col {
        border-radius: 4px;
      }
      .inp {
        width: 100%;
        margin: 0 auto;
        margin-top: 15%;
        input {
          height: 42px;
        }
        .inpu {
          border: 1px solid #2196f3 !important;
          border-radius: 3px;
        }
      }
      .el-input__inner {
        height: 32px;
      }
    }
    #copyright {
      .con {
        height: 26px;
        line-height: 26px;
        text-align: center;
        color: #4e4a4a;
        font-size: 12px;
        a {
          text-decoration: none;
          color: #4e4a4a;
          margin: 0 10px;
        }
      }
    }
  }
}
</style>



